<template>
  <div>
    <button type="button" @click="goBack">Go Back</button>
    <div class="template">
      <img draggable="false" :src="product.url" alt="">
      <p>{{ product.name }}</p>
    </div>
  </div>
</template>

<script>
import products from '@/mixins/getItems.js'
export default {
  name: 'viewProduct',
  mixins: [products],
  props: ['productId'],
  data () {
    return {
      items: [],
      product: []
    }
  },
  methods: {
    goBack () {
      this.$router.go(-1) // this.$router.push('/')
    },
    getItem () {
      var item = []
      for (var i of this.items) {
        if (i.id === parseInt(this.productId)) {
          item = i
        }
      }
      return item
    }
  },
  created () {
    this.product = this.getItem()
  }
}
</script>
<style scoped>
h1 {
  position: relative;
  color: azure;
  text-align: center;
}
.template {
  height: auto;
  width: 400px;
  background-color: white;
  justify-content: center;
  border: 10px solid rgb(235, 103, 115);
  margin: auto;
}
.template>p {
  display: flex;
  justify-content: center;
  color: black;
  font-size: 40px;
  height: auto;
  margin: 0px;
  cursor: default;
}
.template>img {
  width: 100%;
  height: 300px;
}
</style>
