<template>
  <main class="order">
    <form method="post" @submit.prevent="itemsUpdate()">
      <table>
        <thead>
          <tr>
            <th>Item Name</th>
            <th>Item In Stock</th>
            <th>Quantity (in Carton)</th>
            <th>Add /Subtract</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in items" :key="index">
            <td style="text-align:left;"> {{ item.name }} </td>
            <td style="text-align:center;"> {{ item.instock }} </td>
            <td style="text-align:center;"> {{ item.quantity }} </td>
            <td><button :class="{disabled: item.quantity>=100 || item.quantity>=item.instock}" type="button" :disabled= "item.quantity>100 || item.quantity>=item.instock" @click="item.quantity++">+</button><button :class="{disabled: item.quantity<=0}" type="button" :disabled="item.quantity<=0" @click="item.quantity--">-</button></td>
          </tr>
          <tr>
            <td></td>
            <td style="text-align:right;"> Total :</td>
            <td merge>{{ total }}</td>
            <td><input type="submit" class="submit" value="SUBMIT" :class="{disabled: total<1}" :disabled="total<1"></td>
          </tr>
          <tr>
            <input type="radio" name="orderType" v-model="orderType" value="State">State
            <input type="radio" name="orderType" v-model="orderType" value="Inter-State">Inter-State
          </tr>
        </tbody>
        <select name="orderOpt" v-model="orderOpt" placeholder="Select Any Option">
          <option value="Deliver">Deliver It</option>
          <option value="Pickup">Pickup By Myself</option>
        </select>
      </table>
    </form>
  </main>
</template>

<script>
export default {
  name: 'order',
  data () {
    return {
      orderOpt: 'Deliver',
      orderType: 'State',
      items: [],
      item: null
    }
  },
  created () {
    this.$http.get('https://api.jsonbin.io/b/5efb0b180bab551d2b694087')
      .then(
        response => {
          console.log('Server is Up and Running')
          return response.body // on success do this
        },
        response => {
          setTimeout(function () {
            alert('The Server is not Running') // on failure do this
          }, 5000)
        }
      )
      .then(data => { this.items = data })
  },
  computed: {
    total () {
      let sum = 0
      for (let i = 0; i < this.items.length; i++) {
        sum += this.items[i].quantity
      }
      return sum
    }
  },
  methods: {
    itemsUpdate () {
      this.items.forEach((item, i) => {
        item.instock -= item.quantity
        item.quantity = 0
      })
      this.put()
    },
    put () {
      this.$http.put('http://localhost:3000/items', this.items)
        .then(
          response => {
            alert('Order Placed Successfully')//  Success Callback
          },
          response => {
            alert('Order is not placed due to some ERROR. Please try again.')// Error Callback
          }
        )
    }
  },
  watch: {
  }
}
</script>
<style scoped>
table {
  font-size: 8px;
  position: relative;
  margin: auto;
  font-family: Helvetica;
  text-align: center;
  background: transparent;
}
table {
  background: rgba(255,250,240,1);
  width: 70%;
}
th {
  background: rgba(230,230,250,0.5);
  height: 3vh;
}
tr:nth-child(odd) {
  background: rgb(250,240,230);
}
td {
  padding: 0.1vh;
  width: 40%;
}
button {
  background-color: rgb(240,248,255);
  border: none;
  width: 50%;
  text-align: center;
}
.disabled {
  opacity: 0.5;
}
.submit {
  width: 70px;
  height: 20px;
  background-color: rgb(240,248,255);
  border-radius: 35px;
  border: none;
  text-align: center;
  cursor: pointer;
  padding: auto;
}
@media screen and (min-width: 720px) {
  table {
    font-size: 20px;
  }
  button {
    cursor: pointer;
    font-size: 20px;
    height: 20px;
    overflow: hidden;
    width: 40px;
    margin: auto;
    transition: all .5s;
  }
  button:active {
    box-shadow: none;
  }
  .submit {
    width: 110px;
    height: 30px;
  }
}
</style>
